          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingOne">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                Como administrar melhor a minha vida financeira?
              </button>
            </h2>
            <div id="collapseOne" class="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <h4>1. Conheça seu dinheiro</h4>
                  <p>É preciso levar em conta suas receitas e despesas, incluindo o que é fixo e o que
                  varia.</p>

                  <h4>2. Descubra gastos supérfluos</h4>
                  <p>Os gastos supérfluos são grandes inimigos da saúde financeira. Ao invés de
                  gastar com coisas desse tipo você poderia estar investindo seu dinheiro.
                  A gente tem que acabar com essa ideia de que investir exige muito
                  dinheiro, tempo e conhecimento. A realidade é que existem diversos
                  investimentos bem tranquilos e que podem ser iniciados com pouco
                  dinheiro.</p>

                  <h4>3. Limite suas compras com cartão de crédito</h4>
                  <p>Os cartões de crédito podem se tornar o pior inimigo de quem gasta mal.
                  Quando você fica sem dinheiro, basta recorrer aos seus cartões de crédito
                  sem considerar se terá condições de pagar o saldo. Resista à tentação de
                  usar seus cartões de crédito para compras que você não pode pagar,
                  especialmente em itens de que você realmente não precisa.</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingTwo">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                O que é necessário para abrir um escritório de contabilidade?
              </button>
            </h2>
            <div id="collapseTwo" class="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <p>A contabilidade é uma área crucial nas empresas, pois controla vários processos fundamentais do negócio da abertura a folha de pagamento dos funcionários.
                  Com isso, os contadores são profissionais bastante requisitados.</p>

                  <h4>1. Fazer o registro no Conselho Regional de Contabilidade (CRC)</h4>
                  <p>O primeiro item do que é necessário para abrir um escritório de contabilidade é ter o registro no CRC.</p>

                  <h4>2. Fazer registros nos demais órgãos necessários</h4>
                  <p>Além do CRC, o registro em outros órgãos públicos também é necessário para abrir um escritório de contabilidade. Entre eles estão:<br>
                  Registro na Junta Comercial;<br>
                  Registro na Secretaria da Receita Federal (para obter o CNPJ);<br>
                  Registro na Secretaria Estadual de Fazenda;<br>
                  Registro para obtenção de alvará de funcionamento na prefeitura do município;<br>
                  Registro na Entidade Sindical Patronal (é obrigatório fazer a Contribuição Sindical Patronal anualmente);<br>
                  Registro no sistema Conectividade Social – INSS/FGTS da Caixa Econômica Federal;<br>
                  Licença de funcionamento junto ao Corpo de Bombeiros Militar local;<br>
                  Consulta de local e emissão das certidões, inclusive Alvará de Funcionamento, na prefeitura.</p>

                  <h4>3. Estabelecer os objetivos do negócio</h4>
                  <p>Passado essa etapa burocrática, pensar na estratégia do seu negócio também é necessário para abrir um escritório de contabilidade.
                  Ao iniciar um novo negócio, é extremamente importante definir metas que o ajudarão a seguir em frente.
                  As metas que você definir serão o coração da seu escritório de contabilidade nos primeiros dois meses e vão desempenhar um papel enorme no sucesso que sua empresa vai ter.
                  Então, pense em quantos clientes você deseja que sua empresa tenha. E quanto lucro você deseja obter.
                  Depois de definir todos os seus objetivos, você pode até anotá-los e mantê-los em um lugar onde possa vê-los todos os dias.
                  Faça uma revisão das suas metas e objetivos para fazer os ajustes necessários de tempos em tempos, conforme a empresa evoluir.</p>

                  <h4>4. Formar uma equipe estratégica</h4>
                  <p>Outro ponto que é necessário para abrir um escritório de contabilidade é fazer um recrutamento rigoroso da equipe de trabalho.
                  Se tem algo que todo empreendedor concorda é que empregar as pessoas certas é fundamental. Pense que as pessoas da sua equipe devem ser tão ou mais inteligentes e capacitadas que você.</p>

                  <h4>5. Escolher um software para automatizar processos</h4>
                  <p>Outro ponto que é necessário para abrir um escritório de contabilidade é escolher o software que você usará na sua empresa.
                  Com tantas novas empresas sendo abertas, há uma ampla variedade de softwares de contabilidade que você pode escolher.</p>

                  <h4>6. Pense na estratégia de marketing</h4>
                  <p>Pensar na estratégia para atrair clientes para seu escritório de contabilidade é fundamental.</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingThree">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                Como funciona o setor financeiro de uma empresa?
              </button>
            </h2>
            <div id="collapseThree" class="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <p>O setor financeiro é responsável pelo desempenho e gestão das finanças de uma empresa.
                  Então é seu dever garantir recursos e uma boa gestão do patrimônio do negócio.
                  Para isso exerce funções como: contabilidade, tesouraria, gestão das finanças, controle de tributos e gestão de custos.</p>

                  <h4>Contabilidade</h4>
                  <p>De grande importância para a empresa, a Contabilidade organiza, documenta e analisa todos os passivos e ativos patrimoniais de uma empresa.
                  Como ativo entende-se todos os bens que a empresa possui, como imóveis, equipamentos, investimentos e capital em caixa.
                  Já passivos representam o que a empresa deve: pagamentos e financiamentos, por exemplo.</p>

                  <h4>Tesouraria</h4>
                  <p>Aqui é onde o controle financeiro acontece. Fluxo de caixa e plano de contas são algumas das ferramentas em que o setor de tesouraria utiliza no dia a dia</p>

                  <h4>Gestão de tributos</h4>
                  <p>Empresas, principalmente brasileiras, lidam com tributos e impostos o tempo inteiro.
                  Ter um setor (ou função) dentro do departamento financeiro garante o melhor pagamento de tributos possível</p>

                  <h4>Demonstrações financeiras</h4>
                  <p>As demonstrações financeiras são relatórios que indicam o desempenho financeiro da empresa em um determinado período de tempo</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingFive">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFive" aria-expanded="true" aria-controls="collapseFive">
                Como administrar melhor a minha vida financeira?
              </button>
            </h2>
            <div id="collapseFive" class="accordion-collapse collapse" aria-labelledby="headingFive" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <p>O controle financeiro pessoal é a forma mais básica de cuidar do seu
                  dinheiro.</p>
                  <p>Administrando bem suas finanças, você estará ciente das suas
                  receitas e despesas e de outros aspectos financeiros.
                  Além disso, o controle financeiro pessoal traz a possibilidade de multiplicar
                  seu dinheiro. Por meio do pleno conhecimento de sua renda adquirido
                  graças ao controle. Há mais possibilidades de ter dinheiro sobrando no fim
                  do mês, que pode ser usado para investimentos, por exemplo.</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingSix">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseSix" aria-expanded="true" aria-controls="collapseSix">
                Dicas de economia financeira para melhorar a relação com o dinheiro
              </button>
            </h2>
            <div id="collapseSix" class="accordion-collapse collapse" aria-labelledby="headingSix" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <h4>1. Anote o dinheiro que entra e sai da sua conta</h4>
                  <p>O primeiro passo é saber quanto você recebe e quanto gasta. Para isso, você pode usar criar uma planilha e anotar todo o dinheiro que entra e sai da sua conta bancária.
                  Esse destrinchamento de gastos e recebimentos é uma excelente maneira de ter uma visão completa da sua vida financeira e descobrir onde pode economizar, se necessário.
                  Uma boa maneira de fazer isso é separando os gastos por categorias, como: <strong>moradia; educação; família; alimentação;</strong></p>

                  <h4>2. Faça uma reserva de emergência</h4>
                  <p>Outro passo para ter mais saúde financeira é fazer uma reserva de emergência.
                  Para isso, você terá que se organizar para poupar uma quantia, preferencialmente fixa, todo mês ou sempre que possível.
                  A reserva de emergência serve para salvar na hora do imprevisto. Por isso, o ideal é ter uma quantia que consiga cobrir os seus gastos mensais por um período significativo (de 6 meses a 1 ano, se possível).
                  Dessa maneira, se você precisar se afastar e ficar um tempo sem fonte de renda, ou com gastos maiores do que o previsto, conseguirá pagar as contas sem precisar recorrer a empréstimos.</p>

                  <h4>3. Defina metas</h4>
                  <p>Metas são fundamentais para uma vida financeira de qualidade e, claro, para a realização dos seus objetivos.
                  Por isso, pense e registre tudo o que você pretende alcançar a curto, médio e longo prazo. Inclua, ainda, tudo o que precisa fazer para atingir as suas metas, como: cortar gastos supérfluos, fazer um trabalho extra para complementar a renda, investir uma porcentagem x dos seus ganhos etc.
                  Com metas definidas e um passo a passo completo do que precisa fazer, você pode começar a implementar ações práticas e que contribuirão para você chegar onde deseja.</p>

                  <h4>4. Estude sobre o assunto</h4>
                  <p>Se tivéssemos contato com a educação financeira nas escolas desde a infância, seria muito mais fácil falar de dinheiro e cuidar das nossas finanças, não é mesmo?
                  Mas enquanto essa não é a realidade, pelo menos para a maior parte da população, precisamos buscar conhecimento por conta própria.
                  Na sua jornada rumo à economia financeira, estude bastante sobre o assunto para conhecer as diversas possibilidades. Leia sobre o tema, escute podcasts, faça cursos, converse com amigos e familiares…
                  Quanto mais você falar e ouvir sobre dinheiro, mais segurança terá para tomar decisões e criar hábitos financeiros mais saudáveis.</p>

                  <h4>5. Organize a sua rotina financeira</h4>
                  <p>Hoje, usamos aplicativos para praticamente tudo: transporte, pedir comida, conversar com as pessoas, fazer reuniões e até mesmo para relacionamentos! Então por que não aproveitar a tecnologia também na rotina financeira?
                  Existem muitos apps focados em gestão financeira pessoal, como GuiaBolso, Minhas Finanças e Organizze Finanças.
                  A vantagem de contar com esses recursos para controlar as finanças é ter espaço para definir metas, separar os gastos por categorias, gerenciar recebimentos e acompanhar diariamente as transações da sua conta bancária.</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingSeven">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseSeven" aria-expanded="true" aria-controls="collapseSeven">
                Como faço para vender mais no meu comércio?
              </button>
            </h2>
            <div id="collapseSeven" class="accordion-collapse collapse" aria-labelledby="headingSeven" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <h4>1. Saia para vender:</h4>
                  <p>Não fique esperando que a venda ocorra, seja mais ativo. Escolha os horários de menos movimento e incentive os seus vendedores a ligar, enviar e-mails ou até mesmo usar o Whatsapp para divulgar ofertas e convidar o cliente para ir até a loja. Mas atenção, o vendedor tem que ser estimulado sempre: crie uma rotina para estes novos hábitos e reforce as novas estratégias em reuniões semanais.</p>

                  <h4>2. Realize reuniões semanais breves e objetivas:</h4>
                  <p>As pessoas precisam de um momento para conversar sobre metas e falar de problemas dos produtos e de clientes. Também é um ótimo momento para os fornecedores treinarem sua equipe sobre seus produtos. Estes encontros não precisam ser longos: 30 minutos são suficientes. Para muitos varejistas, estes pequenos treinamentos são perda de tempo, mas cuidado: ao abrir mão destas reuniões, você terá uma equipe sem preparo para atender bem a seus clientes, ou seja, no futuro, isso comprometerá o desempenho das suas vendas.</p>

                  <h4>3. Compre bem para vender melhor:</h4>
                  <p>Para o comércio ter sucesso, um dos segredos é comprar muito bem dos fornecedores, negociar ao extremo e se possível pagar à vista. No entanto, nos dias de hoje você não pode comprar apenas pelo lucro. Escolha mercadorias que atendam às necessidades das pessoas. Encher sua loja de mercadorias sem esta seleção estratégica pode resultar em futuras remarcações, que só tiram o lucro e atrapalham os resultados da sua empresa.</p>

                  <h4>4. Esteja presente no mundo digital:</h4>
                  <p>Você utiliza o seu smartphone para pesquisar produtos, comprar e se relacionar com marcas e pessoas, não é? Então, por que ainda tem tanta resistência em investir de verdade em uma presença digital para o seu negócio?! O consumidor está online e temos muita tecnologia disponível. Lembre-se: por meio da Internet, você pode vender para qualquer lugar do mundo.Comece cadastrando seu negócio no Google: ele sempre privilegia empresas locais em buscas. As redes sociais também são ótimas para os negócios: use-as para conversar/se relacionar com seu consumidor, levar novidades e convidar para lançamentos. Quer ir mais longe, e vender pela internet? Procure um consultor especializado em e-commerce e conheça bem as ferramentas de vendas online antes de investir.</p>

                  <h4>5. Expanda seu negócio:</h4>
                  <p>Muitos empresários acreditam que para expandir seu negócio, é preciso aumentar o número de lojas. Mas a expansão também pode ser feita por meio da diversificação dos canais de atendimento, criação de nichos diferentes para pessoa jurídica e física, separação das vendas por volume, participação em licitações e oferta ou atendimento diferenciando para clientes especiais. Lembre-se: crescer uma empresa é aumentar lucros e não os problemas.</p>

                  <h4>6. Venda mais que produtos:</h4>
                  <p>Não adianta apenas vender produtos, os clientes querem ser bem atendidos. Tente agregar serviços a seu negócio, como entregas com hora marcada, treinamento para produtos especiais e demonstração de produtos em domicílio.</p>

                  <h4>7. Personalize o seu atendimento:</h4>
                  <p>Procure conhecer melhor seu cliente: saiba o que ele gosta e o que o interessa. As pessoas não gostam de receber mensagens sobre produtos que não lhes interessam ou que não tenham nada a haver com elas. Crie uma lista com o nome do consumidor, dados básicos para entrar em contato com ele e seu gosto. Não se esqueça de demonstrar interesse por ele. Se você se interessa pelo seu cliente, naturalmente ele se interessará por você.</p>

                  <h4>8. Vá para a linha de frente e tire proveito das críticas:</h4>
                  <p>Vá para o balcão e converse com seus clientes. Quando começamos o nosso negócio estamos sempre em contato com nosso cliente, mas ao crescermos um pouco, temos que ir para atrás de uma mesa administrar, certo? Pois é, tire um tempo e vá para o balcão conversar com as pessoas, e esteja pronto para ouvir críticas. E quando ouvir, não fique justificando, anote todas, e use na sua reunião semanal com a equipe. A crítica ensina mais que um elogio.</p>

                  <h4>9. Inove no jeito de comunicar:</h4>
                  <p>Sim, a propaganda ainda é muito importante, mas não fique só na distribuição de folders. Estude novos caminhos, experimente novos formatos! Use as redes sociais para estreitar laços com os consumidores e divulgue sua empresa na Internet. Estes novos formatos estão dando muito certo no mundo todo. Procure especialistas em publicidade, e se não achar ou puder pagar, o Youtube está cheio de vídeos que podem lhe orientar na divulgação do negócio.</p>

                  <h4>10. Ponha as boas práticas no papel:</h4>
                  <p>A sua equipe não pode adivinhar as boas práticas do seu negócio. Então, crie um “regimento interno” para o seu negócio. Um documento impresso que possa ser consultado e nortear os funcionários a agirem conforme as estratégias definidas, garantido o sucesso da empresa.</p>
              </div>
            </div>
          </div>
          <div class="accordion-item mt-3 border">
            <h2 class="accordion-header" id="headingEight">
              <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseEight" aria-expanded="true" aria-controls="collapseEight">
                Como pagar menos impostos legalmente?
              </button>
            </h2>
            <div id="collapseEight" class="accordion-collapse collapse" aria-labelledby="headingEight" data-bs-parent="#accordionExample">
              <div class="accordion-body">
                  <h4>O que pode ser feito para pagar menos impostos?</h4>
                  <p>Agora que já vimos tudo isso, é hora de entender como é possível pagar menos impostos de maneira legalizada e sem nenhum tipo de problemas com a Receita Federal. Veja algumas opções abaixo!</p>

                  <h4>Planejamento tributário</h4>
                  <p>A primeira ferramenta para reduzir a carga tributária de uma empresa é fazer o seu planejamento tributário. Com ele, é possível identificar todos os tributos que serão pagos pelo negócio e estudar todas as opções para entender qual a melhor configuração.
                  Por exemplo, o Fator R do Simples Nacional pode fazer uma empresa ser enquadrada no Anexo III ou no Anexo V. A diferença entre eles é que um é mais vantajoso para negócios que faturam menos e outro é mais interessante para quem fatura mais.
                  Por isso, o planejamento tributário ajuda a entender como trabalhar o seu Fator R para se enquadrar na melhor opção possível dentro das alíquotas do Simples Nacional.</p>

                  <h4>Incentivos fiscais</h4>
                  <p>As 3 esferas do Poder Executivo no Brasil (Governo Federal, Estadual e Municipal) têm a capacidade de oferecer incentivos fiscais e benefícios tributários para empresas. Por isso, vale a pena analisar todas as opções disponíveis e ver quais são os benefícios disponíveis para o seu negócio. Em muitos casos, por exemplo, trocar a sede da empresa para outra cidade pode ser mais vantajoso.</p>

                  <h4>Terceirização</h4>
                  <p>Por fim, existe o fato de que a Terceirização pode ajudar a empresa a reduzir a sua carga tributária. Em vez de pagar impostos patronais e contribuições para o INSS de funcionários contratados, a empresa pode ganhar ao contratar uma empresa terceirizada para cuidar do mesmo serviço. No entanto, é vital entender quando essa terceirização traz benefícios reais e quando ela prejudica a qualidade do seu produto ou serviço, atrapalhando seu crescimento em longo prazo.</p>

                  <p>Agora que você já entende o que é Elisão Fiscal e quais os benefícios que ela pode trazer para o seu negócio, é hora de estudar maneiras de reduzir a sua carga tributária legalmente, sem correr riscos de infringir a lei e ter problemas com a Receita Federal. Para isso, é vital ter o apoio de uma consultoria de Contabilidade Digital que tenha posicionamento estratégico e possa montar um plano para reduzir os seus tributos.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
